
import React, { PureComponent } from 'react'
import { setTimeout } from 'timers'

export default class _app extends PureComponent {
  constructor(props) {
    super(props)

    this.state = {
      tictoc: true,
      msg: 'block',
      mainbox: 'none',
    }
  }

componentDidMount(){
 setTimeout(() => { this.setState({
  mainbox: 'block',
  msg: 'none'
})}, 5000)
}

callBox(){
  this.setState({
    mainbox: 'block',
    msg: 'none'
  })
  console.log('box called')
}

setKey = (e) => {
  var id = e.target.id;
  var val;
  this.setState({
      tictoc: !this.state.tictoc
  })
  if(this.state.tictoc == true){
      val = 'X'
  }
  else{
    val = 'O'
  }
  document.getElementById(id).innerHTML = val
  console.log(val)
}

  render() {
    return (
      <div id='main' className='body'>
       <h1 className='msg'>Welcome to Tic-Toc-Toe</h1>
        <div className='mainBox'>
          <div className='rowBox'>
            <div
              id='u1' className='lu sepBox'
              onClick={(e) => this.setKey(e)}></div>
            <div
              id='u2' className=' sepBox'
              onClick={(e) => this.setKey(e)}></div>
            <div
              id='u3' className='ru sepBox'
              onClick={(e) => this.setKey(e)}></div>
          </div>
          <div className='rowBox'>
            <div
              id='m1' className=' sepBox'
              onClick={(e) => this.setKey(e)}></div>
            <div
              id='m2' className='m sepBox'
              onClick={(e) => this.setKey(e)}></div>
            <div
              id='m3' className='sepBox'
              onClick={(e) => this.setKey(e)}></div>
          </div>
          <div className='rowBox'>
            <div
              id='l1' className='ll sepBox'
              onClick={(e) => this.setKey(e)}></div>
            <div
              id='l2' className=' sepBox'
              onClick={(e) => this.setKey(e)}></div>
            <div
              id='l3' className='rl sepBox'
              onClick={(e) => this.setKey(e)}></div>
          </div>
        </div>

        <style jsx>
          {`
              .body {
                background-color: black;
                width: 100vh;
                height: 100vh;
                display: flex;
                justify-content: center;
                align-items: center;
              }
              .mainBox {
                height: 600px;
                width: 600px;
                cursor: pointer;
                display: ${this.state.mainbox}
                }
              .lu{
                border-color: white;
                border-style: solid;
                border-width: 0px 1px 1px 0px;
              }
              .msg{
                display: ${this.state.msg};
                color: white;
              }
              .ru{
                border-color: white;
                border-style: solid;
                border-width: 0px 0px 1px 1px;
              }
              .m{
                border-color: white;
                border-style: solid;
                border-width: 1px;
              }
              .ll{
                border-color: white;
                border-style: solid;
                border-width: 1px 1px 0px 0px;
              }
              .rl{
                border-color: white;
                border-style: solid;
                border-width: 1px 0px 0px 1px;
              }
              .rowBox{
                height: 33.33%;
                display: flex;
              }
              .sepBox{
                width: 33.33%;
                display: flex;
                color: white;
                justify-content: center;
                align-items: center;
                font-size: 12em;
              }
          `}
        </style>
      </div>
    )
  }
}
